body {
    width:100%;
    height:100%;
    left:0;
    right:0;
    margin:auto;
    background-color: rgb(100, 100, 100);
}

#videos {
    position:relative;
    /* display:grid;
    grid-template-columns: 1fr 1fr;
    /* column-gap:1em; */
    left:0;
    right:0;
    width:96vw;
    height:60vw;
    margin: 2vw  auto;    
    /* padding:10px; */ 
}

#user-1-video {
    position:absolute;
    width:100%;
    height:90%;
    z-index: 0;
    
}

#user-2-video {
    position:absolute;
    right:0;
    z-index:1;
    width:33%;
    height:30/2%;
    background-color: rgb(62, 62, 62);
}

video {
    background-color: rgb(62, 62, 62);
    transform: scaleX(-1);
    border-radius: 30px;
    box-shadow: 0px 5px 20px rgb(0, 0, 0, .2)
}

#inputs{
    position:absolute;

}

.containter {
    
    overflow: auto;

}

.input-label, .input-button, .input-area {
    border-radius: 5px;
    box-shadow: 0px 5px 20px rgb(0, 0, 0, .2);
    background-color: rgb(200, 200, 200);
    margin: 5px;
}


@media screen and (max-width:100%) {
    body {
        width:100%;
    }
}